import React from "react";
import "../styles/TemplateSelector.css"; // Correct CSS import

const TemplateSelector = ({ onSelectTemplate }) => {
  return (
    <div className="template-selector">
      <h2>Select a Template</h2>
      <div className="template-options">
        <div
          className="template-option"
          onClick={() => onSelectTemplate(1)}
        >
          <img
            src={require("../assets/John_Doe_Resume_1.jpg")}
            alt="Template 1"
          />
          <button>Template 1</button>
        </div>
        <div
          className="template-option"
          onClick={() => onSelectTemplate(2)}
        >
          <img
            src={require("../assets/John_Doe_Resume_2.jpg")}
            alt="Template 2"
          />
          <button>Template 2</button>
        </div>
        <div
          className="template-option"
          onClick={() => onSelectTemplate(3)}
        >
          <img
            src={require("../assets/John_Doe_Resume_3.jpg")}
            alt="Template 3"
          />
          <button>Template 3</button>
        </div>
      </div>
    </div>
  );
};

export default TemplateSelector;
