import React from "react";
import "../styles/ResumePreview.css";

export default function ResumePreview({
  formData,
  selectedTemplate,
  onSwitchTemplate,
}) {
  const renderExperience = (experience) => (
    <div className="experience-item">
      <h4>{experience.jobTitle}</h4>
      <p>
        {experience.employer}, {experience.location}
      </p>
      <p>{`${experience.startDate.month} ${experience.startDate.year} - ${
        experience.isCurrent
          ? "Present"
          : `${experience.endDate.month} ${experience.endDate.year}`
      }`}</p>
    </div>
  );

  const renderEducation = (education) => (
    <div className="education-item">
      <h4>
        {education.degree} in {education.fieldOfStudy}
      </h4>
      <p>
        {education.schoolName}, {education.schoolLocation}
      </p>
      <p>
        Graduated:{" "}
        {`${education.graduationDate.month} ${education.graduationDate.year}`}
      </p>
    </div>
  );

  return (
    <div className="resume-preview-container">
      <div className="template-switcher">
        <button onClick={onSwitchTemplate}>Switch Template</button>
      </div>
      <div
        id="resume-preview"
        className={`resume-preview template-${String(selectedTemplate)}`}
      >
        <header>
          <h1>
            {formData.personalInfo.firstName} {formData.personalInfo.surname}
          </h1>
          <p>
            {formData.personalInfo.email} | {formData.personalInfo.phone}
          </p>
          <p>
            {formData.personalInfo.district}, {formData.personalInfo.postalCode}
            , {formData.personalInfo.cityCountry}
          </p>
          <p className="summary">{formData.personalInfo.professionalSummary}</p>
        </header>

        <section className="experience">
          <h2>Work Experience</h2>
          {formData.experience.map((exp, index) => (
            <React.Fragment key={index}>{renderExperience(exp)}</React.Fragment>
          ))}
        </section>

        <section className="education">
          <h2>Education</h2>
          {formData.education.map((edu, index) => (
            <React.Fragment key={index}>{renderEducation(edu)}</React.Fragment>
          ))}
        </section>

        <section className="skills">
          <h2>Skills</h2>
          <ul>
            {formData.skills.map((skill, index) => (
              <li key={index}>{skill}</li>
            ))}
          </ul>
        </section>
      </div>
    </div>
  );
}
