import React from "react";

const months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

const currentYear = new Date().getFullYear();
const years = Array.from({ length: 51 }, (_, i) => currentYear - i);

export default function Education({ data, updateData }) {
  const handleChange = (index, field, value) => {
    const updatedEducation = data.map((edu, i) =>
      i === index ? { ...edu, [field]: value } : edu
    );
    updateData(updatedEducation);
  };

  const handleDateChange = (index, field, value) => {
    const updatedEducation = data.map((edu, i) =>
      i === index
        ? { ...edu, graduationDate: { ...edu.graduationDate, [field]: value } }
        : edu
    );
    updateData(updatedEducation);
  };

  const addEducation = () => {
    updateData([
      ...data,
      {
        schoolName: "",
        schoolLocation: "",
        degree: "",
        fieldOfStudy: "",
        graduationDate: { month: "", year: "" },
      },
    ]);
  };

  const removeEducation = (index) => {
    updateData(data.filter((_, i) => i !== index));
  };

  return (
    <div className="form-section">
      <h3>Education</h3>
      {data.map((education, index) => (
        <div
          key={index}
          className="education-item"
        >
          <div className="form-group">
            <label htmlFor={`schoolName-${index}`}>School Name: *</label>
            <input
              id={`schoolName-${index}`}
              type="text"
              value={education.schoolName}
              onChange={(e) =>
                handleChange(index, "schoolName", e.target.value)
              }
              placeholder="e.g. ABC University"
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor={`schoolLocation-${index}`}>School Location:</label>
            <input
              id={`schoolLocation-${index}`}
              type="text"
              value={education.schoolLocation}
              onChange={(e) =>
                handleChange(index, "schoolLocation", e.target.value)
              }
              placeholder="e.g. Owerri, Imo"
            />
          </div>
          <div className="form-group">
            <label htmlFor={`degree-${index}`}>Degree:</label>
            <input
              id={`degree-${index}`}
              type="text"
              value={education.degree}
              onChange={(e) => handleChange(index, "degree", e.target.value)}
              placeholder="e.g. Bachelor of Science"
            />
          </div>
          <div className="form-group">
            <label htmlFor={`fieldOfStudy-${index}`}>Field of Study:</label>
            <input
              id={`fieldOfStudy-${index}`}
              type="text"
              value={education.fieldOfStudy}
              onChange={(e) =>
                handleChange(index, "fieldOfStudy", e.target.value)
              }
              placeholder="e.g. Microbiology"
            />
          </div>
          <div className="form-group">
            <label>Graduation Date:</label>
            <select
              value={education.graduationDate.month}
              onChange={(e) => handleDateChange(index, "month", e.target.value)}
            >
              <option value="">Select Month</option>
              {months.map((month, i) => (
                <option
                  key={i}
                  value={month}
                >
                  {month}
                </option>
              ))}
            </select>
            <select
              value={education.graduationDate.year}
              onChange={(e) => handleDateChange(index, "year", e.target.value)}
            >
              <option value="">Select Year</option>
              {years.map((year, i) => (
                <option
                  key={i}
                  value={year}
                >
                  {year}
                </option>
              ))}
            </select>
          </div>
          <button
            type="button"
            onClick={() => removeEducation(index)}
            className="remove-btn"
          >
            Remove
          </button>
        </div>
      ))}
      <button
        type="button"
        onClick={addEducation}
      >
        Add Another Education
      </button>
    </div>
  );
}
