import React, { useState } from "react";
import FormContainer from "./components/FormContainer";
import TemplateSelector from "./components/TemplateSelector";
import ResumePreview from "./components/ResumePreview";
import html2pdf from "html2pdf.js";
import "./App.css";

export default function Component() {
  const [formData, setFormData] = useState(null);
  const [selectedTemplate, setSelectedTemplate] = useState(null);

  const handleFormSubmit = (data) => {
    setFormData(data);
  };

  const handleTemplateSelect = (templateId) => {
    setSelectedTemplate(templateId);
  };

  const downloadPDF = () => {
    const element = document.getElementById("resume-preview");
    const h2pSettings = {
      filename: `${formData.personalInfo.firstName}_${formData.personalInfo.surname}_Resume.pdf`,
      image: { type: "jpeg", quality: 1 },
      html2canvas: { scale: 3 },
      jsPDF: { format: "a4", orientation: "portrait" },
    };
    html2pdf().from(element).set(h2pSettings).save();
  };

  const resetForm = () => {
    setFormData(null);
    setSelectedTemplate(null);
  };

  const switchTemplate = () => {
    setSelectedTemplate((prevTemplate) => (prevTemplate % 3) + 1);
  };

  return (
    <div className="app">
      <h1 className="product-name">Resume Builder</h1>
      {!formData ? (
        <FormContainer onSubmit={handleFormSubmit} />
      ) : !selectedTemplate ? (
        <TemplateSelector onSelectTemplate={handleTemplateSelect} />
      ) : (
        <div className="preview-section">
          <h2>Resume Preview (Template {selectedTemplate})</h2>
          <div className="resume-preview-container">
            <ResumePreview
              formData={formData}
              selectedTemplate={selectedTemplate}
              onSwitchTemplate={switchTemplate}
            />
          </div>
          <div className="action-buttons">
            <button
              onClick={downloadPDF}
              className="download-button"
            >
              Download Resume as PDF
            </button>
            <button
              onClick={resetForm}
              className="reset-button"
            >
              Start Over
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
