import React, { useState } from "react";
import PersonalInfo from "./FormPersonalInfo";
import WorkExperience from "./FormWorkExperience";
import Education from "./FormEducation";
import Skills from "./FormSkills";
import ProfessionalSummary from "./FormProfessionalSummary";
import initialFormData from "./resumeFormSchema";
import "../styles/ResumeForm.css";

export default function FormContainer({ onSubmit }) {
  const [step, setStep] = useState(1);
  const [formData, setFormData] = useState(initialFormData);

  const updateFormData = (section, data) => {
    setFormData((prevData) => ({
      ...prevData,
      [section]: data,
    }));
  };

  const handleNext = () => setStep(step + 1);
  const handleBack = () => setStep(step - 1);

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit(formData);
  };

  return (
    <div className="resume-form">
      <h2>Step {step} of 5</h2>
      <form onSubmit={handleSubmit}>
        {step === 1 && (
          <PersonalInfo
            data={formData.personalInfo}
            updateData={(data) => updateFormData("personalInfo", data)}
          />
        )}
        {step === 2 && (
          <WorkExperience
            data={formData.experience}
            updateData={(data) => updateFormData("experience", data)}
          />
        )}
        {step === 3 && (
          <Education
            data={formData.education}
            updateData={(data) => updateFormData("education", data)}
          />
        )}
        {step === 4 && (
          <Skills
            data={formData.skills}
            updateData={(data) => updateFormData("skills", data)}
          />
        )}
        {step === 5 && (
          <ProfessionalSummary
            data={formData.personalInfo}
            updateData={(data) => updateFormData("personalInfo", data)}
          />
        )}
        <div className="form-navigation">
          {step > 1 && (
            <button
              type="button"
              onClick={handleBack}
            >
              Back
            </button>
          )}
          {step < 5 && (
            <button
              type="button"
              onClick={handleNext}
            >
              Next
            </button>
          )}
          {step === 5 && <button type="submit">Submit</button>}
        </div>
      </form>
    </div>
  );
}
