import React from "react";

const months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];

const currentYear = new Date().getFullYear();
const years = Array.from({ length: 51 }, (_, i) => currentYear - i);

export default function WorkExperience({ data, updateData }) {
  const handleChange = (index, field, value) => {
    const updatedExperience = data.map((exp, i) =>
      i === index ? { ...exp, [field]: value } : exp
    );
    updateData(updatedExperience);
  };

  const handleDateChange = (index, dateType, field, value) => {
    const updatedExperience = data.map((exp, i) =>
      i === index
        ? { ...exp, [dateType]: { ...exp[dateType], [field]: value } }
        : exp
    );
    updateData(updatedExperience);
  };

  const addExperience = () => {
    updateData([
      ...data,
      {
        jobTitle: "",
        employer: "",
        location: "",
        startDate: { month: "", year: "" },
        endDate: { month: "", year: "" },
        isCurrent: false,
      },
    ]);
  };

  const removeExperience = (index) => {
    updateData(data.filter((_, i) => i !== index));
  };

  return (
    <div className="form-section">
      <h3>Work Experience</h3>
      {data.map((experience, index) => (
        <div
          key={index}
          className="experience-item"
        >
          <div className="form-group">
            <label htmlFor={`jobTitle-${index}`}>Job Title: *</label>
            <input
              id={`jobTitle-${index}`}
              type="text"
              value={experience.jobTitle}
              onChange={(e) => handleChange(index, "jobTitle", e.target.value)}
              placeholder="e.g. Human Resources Manager"
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor={`employer-${index}`}>Employer: *</label>
            <input
              id={`employer-${index}`}
              type="text"
              value={experience.employer}
              onChange={(e) => handleChange(index, "employer", e.target.value)}
              placeholder="e.g. ABCXYZ Limited"
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor={`location-${index}`}>Location:</label>
            <input
              id={`location-${index}`}
              type="text"
              value={experience.location}
              onChange={(e) => handleChange(index, "location", e.target.value)}
              placeholder="e.g. 'Lagos, Nigeria' or 'Remote'"
            />
          </div>
          <div className="form-group">
            <label>Start Date:</label>
            <select
              value={experience.startDate.month}
              onChange={(e) =>
                handleDateChange(index, "startDate", "month", e.target.value)
              }
            >
              <option value="">Select Month</option>
              {months.map((month, i) => (
                <option
                  key={i}
                  value={month}
                >
                  {month}
                </option>
              ))}
            </select>
            <select
              value={experience.startDate.year}
              onChange={(e) =>
                handleDateChange(index, "startDate", "year", e.target.value)
              }
            >
              <option value="">Select Year</option>
              {years.map((year, i) => (
                <option
                  key={i}
                  value={year}
                >
                  {year}
                </option>
              ))}
            </select>
          </div>
          <div className="form-group">
            <label>End Date:</label>
            <select
              value={experience.endDate.month}
              onChange={(e) =>
                handleDateChange(index, "endDate", "month", e.target.value)
              }
              disabled={experience.isCurrent}
            >
              <option value="">Select Month</option>
              {months.map((month, i) => (
                <option
                  key={i}
                  value={month}
                >
                  {month}
                </option>
              ))}
            </select>
            <select
              value={experience.endDate.year}
              onChange={(e) =>
                handleDateChange(index, "endDate", "year", e.target.value)
              }
              disabled={experience.isCurrent}
            >
              <option value="">Select Year</option>
              {years.map((year, i) => (
                <option
                  key={i}
                  value={year}
                >
                  {year}
                </option>
              ))}
            </select>
          </div>
          <div className="form-group">
            <label>
              <input
                type="checkbox"
                checked={experience.isCurrent}
                onChange={(e) =>
                  handleChange(index, "isCurrent", e.target.checked)
                }
              />
              I currently work here
            </label>
          </div>
          <button
            type="button"
            onClick={() => removeExperience(index)}
            className="remove-btn"
          >
            Remove
          </button>
        </div>
      ))}
      <button
        type="button"
        onClick={addExperience}
      >
        Add Another Job
      </button>
    </div>
  );
}
